<template>
  <div>
    <div class="title">
        <h3>修改头像</h3>
        <hr>
    </div>
    <el-card class="box-card">
      <div>
        <!-- 图片，用来展示用户选择的头像 -->
        <img v-if="!avatar" class="the_img" src="@/assets/avatar/1.png" alt="" />
        <img v-else class="the_img" :src="avatar" alt="">
        <!-- 按钮区域 -->
        <div class="btn-box">
          <input type="file" accept="image/*" style="display: none" ref="iptRef" @change="onFileChange" />
          <el-button type="primary" icon="el-icon-plus" @click="chooseImg">选择图片</el-button>
          <el-button type="success" icon="el-icon-upload" :disabled="avatar === ''" @click="uploadFn">上传头像</el-button>
        </div>
      </div>
      <div class="tips">头像上传大小不超过80kb</div>
    </el-card>
  </div>

</template>

<script>
import { updateUserAvatarAPI } from '@/api'
export default {
  name: 'UserAvatar',
  data () {
    return {
      avatar: ''  //
    }
  },
  methods:{
    //选择图片
    chooseImg(){
        this.$refs.iptRef.click()
    },
    //选择图片确定了
    onFileChange(e){
        let files = e.target.files //拿到用户选择的文件数组
        if(files.length === 0){

        }else{
            console.log(files[0]);
            //预览解决方案1：文件 -> 内存临时地址
            //语法：URL.createObjectURL(文件)
            //返回值： 内存临时地址
            // this.avatar = URL.createObjectURL(files[0])

            //预览解决方案2：文件 -> base64字符串（这个字符串可以传给后端）
            //1. 创建 FileReader 对象
            const fr = new FileReader()
            // 2. 调用 readAsDataURL 函数，读取文件内容
            fr.readAsDataURL(files[0])
            // 3. 监听 fr 的 onload 事件
            fr.onload = e => {
            // 4. 通过 e.target.result 获取到读取的结果，值是字符串（base64 格式的字符串）
            this.avatar = e.target.result
            }
        }
    },
    //上传头像
    async uploadFn(){
      // console.log(this.avatar);
        const { data: res } = await updateUserAvatarAPI(this.avatar)
        if(res.status !== 0) return this.$message.error(res.message)
        this.$message.success(res.message)
        this.$store.dispatch('getUserInfoActions')
    }
  }
}
</script>

<style scoped>
.title{
    width: 90%;
    margin: 0 auto;
    padding: 20px 0;
    padding-top: 40px;
}
.title h3{
    font-size: 20px;
    color: #543b24;
    padding: 2px 0;
}
hr{
    height: 5px;
    background: #543b24;
    border: none;
    border-radius: 2px;
}
.btn-box {
  margin-top: 10px;
}
.el-card{
  width: 90%;
  margin: 0 auto;
}
.preview {
  object-fit: cover;
}
.the_img{
    width: 350px;
    height: 350px;
}
.el-card{
  background: #ffffff24;
  border-radius: 10px;
}
.tips{
  color: rgb(48, 45, 40);
  padding: 10px;
  font-size: 16px;
}
</style>