<template>
    <div>
        <div class="box">
            <div class="title">
                <h3>我的博客</h3>
                <hr>
            </div>
            <el-card class="box-card" v-for="item in articles" :key="item.id">
            <div slot="header" class="clearfix">
                <span>{{item.title}}</span>
                <span class="isapproval" :class="{'red':item.approval == '2','gray':item.approval == '0','green':item.approval == '1'}">{{item.approvalstr}}</span>
                <el-button style="float: right; padding: 3px 7px; color: red;" type="text" @click="deleteArticle(item)">删除</el-button>
                <el-button style="float: right; padding: 3px 0" type="text" @click="showDialog(item)">修改文章</el-button>
                <!-- 对话框 -->
                <el-dialog title="修改文章" :visible.sync="dialogFormVisible" :modal-append-to-body='false'>
                    <el-form :model="currentArticleInfo" style="width:80%;" :rules="pubFormRules">
                        <el-form-item label="文章标题" label-width="100px" prop="title">
                        <el-input v-model="currentArticleInfo.title" autocomplete="off"></el-input>
                        </el-form-item>
                        <el-form-item label="文章概述" label-width="100px" prop="articledesc">
                        <el-input v-model="currentArticleInfo.articledesc" autocomplete="off"></el-input>
                        </el-form-item>
                        <el-form-item label="文章正文" label-width="100px" prop="content">
                            <quill-editor v-model="currentArticleInfo.content"></quill-editor>
                        </el-form-item>
                        <el-form-item label="文章图片">
                            <el-upload
                            class="upload-demo"
                            drag
                            action="#"
                            ref="uploadRef"
                            :on-change="onChange"
                            :file-list="picFile"
                            :auto-upload="false"
                            accept=".jpeg, .png, .jpg"
                            :before-upload="beforeUpload"
                            multiple>
                            <i class="el-icon-upload"></i>
                            <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                            <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>
                            </el-upload>
                        </el-form-item>

                    </el-form>
                    <div slot="footer" class="dialog-footer">
                        <el-button @click="dialogFormVisible = false">取 消</el-button>
                        <el-button type="primary" @click="updateDialog()">确 定</el-button>
                    </div>
                </el-dialog>
            </div>
            <div class="article-info">
                <div class="article-desc">{{item.desc}}</div>
            </div>
            </el-card>
        </div>
        <!-- 分页器 -->
        <el-pagination
          @current-change="handleCurrentChange"
          @size-change="handleSizeChange"
          style="text-align:center"
          :page-sizes="pageSizes"
          :page-size="pageSize"
          :currentPage="currentPage"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
        </el-pagination>
    </div>
</template>

<script>
export default {
    data(){
      return{
            id: this.$store.state.userInfo.id,
            articles:[],
            currentPage: 1,
            pageSize: 5,
            pageSizes: [3,5],
            total: 0,   //总共的数据条数
            currentArticleInfo:[],
            dialogFormVisible:false,   //对话框显示与隐藏
            //上传图片
            imageUrl: '',
            picFile:[],
            approvalStr:[],
            pubFormRules:{
                title: [
                    {required: true,message: '请输入文章标题！',trigger: 'blur'},
                    {min:1,max:30,message:'文章标题长度为1-30个字符',trigger: 'blur'},
                ],
                articledesc:[
                    {required: true,message: '请输入文章简介！',trigger: 'blur'},
                    {min:1,max:30,message:'文章简介长度为1-30个字符',trigger: 'blur'},
                ],
                content:[
                    {required: true,message: '请输入文章正文！',trigger: 'blur'},
                ]
            }
        }
    },
    mounted(){
        //获取文章列表
        this.getPageList()
        this.getArticlesLength()
    },
    methods:{
        onChange(file){
            this.picFile[this.index] = file
            this.index ++
            console.log(this.picFile);
        },
        async uploadPic(){
            let file = new FormData()
            this.picFile.forEach((item,index)=>{
                file.append('imgFile',this.picFile[index].raw)
            })
            
            axios({
                url:'http://127.0.0.1:2001/admin/addArticlePic',
                method:'POST',
                headers:{
                    'Content-Type': 'multipart/form-data',
                    'Authorization': localStorage.getItem('token')
                },
                data: file,
                params:{
                    articleinfo: this.form
                }
            }).then(res =>{
                    if(res.status == 200){
                        this.$message({
                            type: 'success',
                            message: '图片上传成功',
                        })
                    }else{
                        this.$message({
                            type: 'error',
                            message: '图片上传失败'
                        })
                    }
                })
        },
        beforeUpload(file) {

        },
        //获取文章总条数
        getArticlesLength(){
        this.$axios.get('/admin/getMyBlogUserListLength').then(res=>{
            // console.log(res.data);
            if(res.status == 200){
            this.total = JSON.parse(res.data.data[0].id)
            // console.log(this.total);
            }
        })
        },
        getPageList(){
            this.$axios.get('/admin/getMyBlogUserArticles',{
                params:{
                    pageSize:this.pageSize,
                    currentPage:this.currentPage,
                }
            }).then(res=>{
                if(res.status == 200){
                    this.articles = res.data.data
                }
            })
        },
        handleCurrentChange(pager){
          this.currentPage = pager
          this.getPageList()
        },
        handleSizeChange(limit){
            this.pageSize = limit
          this.getPageList()
        },
        //显示对话框
        showDialog(item){
            this.currentArticleInfo = item 
            this.dialogFormVisible = true        
        },
        //删除文章
        deleteArticle(item) {
            this.currentArticleInfo = item    
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
            }).then(() => {
            this.$axios.delete('/admin/deleteArticle',{
                params:{
                    id: this.currentArticleInfo.id
                }
            }).then(res=>{
                if(res.status == 200){
                    this.$message({
                        type: 'success',
                        message: '删除成功!'
                    });
                }
                this.getPageList()
            })
            }).catch(() => {
            this.$message({
                type: 'info',
                message: '已取消删除'
            });          
            });
        },
        updateDialog(){
            this.dialogFormVisible = false;
            this.$axios.post('/admin/updateArticleInfo',{currentArticleInfo:this.currentArticleInfo,id:this.currentArticleInfo.id}).then(res=>{
                if(res.status == 200){
                    console.log(res.data);
                    this.currentArticleInfo = res.data
                    this.$message({
                        type:'success',
                        message: '修改成功，文章将重新等待审批！'
                    })
                }
            })
        },
    }
}
</script>

<style scoped>
.box{
    width: 90%;
    min-height: 650px;
    margin: 0 auto;
}
.title{
    padding: 20px 0;
    padding-top: 40px;
}
.title h3{
    font-size: 20px;
    color: #543b24;
    padding: 2px 0;
}
hr{
    height: 5px;
    background: #543b24;
    border: none;
    border-radius: 2px;
}
.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both
}
.clearfix span{
    font-size: 14px;
    font-weight: 600;
}
.box-card {
  width: 480px;
}
.el-card.is-always-shadow{
    margin: 10px auto;
    width: 100%;
}
.article-desc{
    color: rgb(82, 82, 82);
    font-size: 12px;
}


/* 上传图片 */
.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
  .isapproval{
    font-weight: 300 !important;
    font-size: 12px !important;
    /* color: green; */
    float: right;
    line-height: 22px;
  }
  .red{
    color: red;
  }
  .gray{
    color: gray;
  }
  .green{
    color: green;
  }
</style>