<template>
<div>
  <div class="box">
    <div class="title">
      <h3>个人资料</h3>
      <hr>
    </div>
    <el-form ref="formRef" :model="form" :rules="formRules" label-width="80px">
      <el-form-item label="用户ID" prop="id">
          <el-input v-model="form.id" :disabled="true"></el-input>
      </el-form-item>
      <el-form-item label="用户名" prop="username">
          <el-input v-model="form.username" :disabled="true"></el-input>
      </el-form-item>
      <el-form-item label="名称" prop="nickname">
          <el-input v-model="form.nickname"></el-input>
      </el-form-item>
      <el-form-item label="邮箱" prop="email">
          <el-input v-model="form.email"></el-input>
      </el-form-item>
      <el-form-item label="权限" prop="right">
          <el-select v-model="form.right" placeholder="访问权限" :disabled="true">
              <el-option label="管理员" value="shanghai"></el-option>
              <el-option label="普通用户" value="beijing"></el-option>
          </el-select>
      </el-form-item>
      <el-form-item label="个人简介" prop="mydesc">
          <el-input type="textarea" v-model="form.mydesc"></el-input>
      </el-form-item>
      <el-form-item>
          <el-button type="primary" @click="onSubmit">保存修改</el-button>
          <el-button type="primary" @click="resetFn">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</div>

</template>

<script>
import { updateUserInfoAPI } from '@/api'
export default {
  data(){
    return{
      form: {
        id: this.$store.state.userInfo.id,
        username: this.$store.state.userInfo.username,
        nickname: this.$store.state.userInfo.nickname,
        email: this.$store.state.userInfo.email,
        right: '',
        mydesc: this.$store.state.userInfo.mydesc ? this.$store.state.userInfo.mydesc : '这位用户很懒，啥都没写哦...'
      },
      formRules:{
        nickname:[
          {required:true, message:'用户名不能为空', trigger:'blur'},
          {min:2, max:32, message:'长度应在2-32之间',trigger:'blur'}
        ],
        email:[
          { required: true, message:'请输入用户邮箱', trigger:'blur'},
          { type: 'email',message:'邮箱格式不正确！',trigger:'blur'},
        ],
      }
    }
  },
  created(){
    console.log(this.$store.state.userInfo);
  },
  mounted(){
    
  },
  methods:{
    //提交修改
    onSubmit(){
      this.$refs.formRef.validate(async valid => {
        if(valid){
          this.form.id = this.$store.state.userInfo.id
          const { data: res } = await updateUserInfoAPI(this.form)
          if(res.status !== 0) return this.$message.error('更新用户信息失败！')
          this.$message.success('更新成功！')
          //重新让vuex获取最新数据
          this.$store.dispatch('getUserInfoActions')
          console.log(res);
        }else{
          return false;
        }
      })
    },
    //重置表单
    resetFn(){
      this.$refs.formRef.resetFields()
    }
  }
}
</script>

<style scoped>
.box{
    width: 90%;
    /* height: 650px; */
    margin: 0 auto;
}
.title{
    padding: 20px 0;
    padding-top: 40px;
}
.title h3{
    font-size: 20px;
    color: #543b24;
    padding: 2px 0;
}
hr{
    height: 5px;
    background: #543b24;
    border: none;
    border-radius: 2px;
}
.el-form{
    margin-top: 20px;
}
.el-form-item {
    margin-bottom: 40px;
}
</style>