<template>
    <div>
      <div class="box">
        <div class="title">
          <h3>管理员申请</h3>
          <hr>
        </div>
        <el-form ref="formRef" :model="form" label-width="80px">
          <el-form-item label="用户名" prop="username">
              <el-input v-model="form.username" disabled></el-input>
          </el-form-item>
          <el-form-item label="邮箱" prop="email">
              <el-input v-model="form.email" disabled></el-input>
          </el-form-item>
          <el-form-item label="申请权限" prop="right">
              <el-select v-model="form.right" placeholder="请选择">
                <el-option label="超级管理员" value="超级管理员"></el-option>
                <el-option label="管理员" value="管理员"></el-option>
                <el-option label="普通用户" value="普通用户"></el-option>
              </el-select>
          </el-form-item>
          <el-form-item label="申请理由" prop="content" style="margin:0;">
            <el-form ref="form" :model="form" label-width="80px" size="medium" prop="content">
              <quill-editor v-model="form.content"></quill-editor>
            </el-form>
          </el-form-item>
          <el-form-item style="padding:10px;margin: 0;">
              <el-button type="primary" @click="onSubmit" style="float: right;">提交申请</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
    
    </template>
    
<script>
import { updateUserInfoAPI } from '@/api'
import { SubmitApplication } from '@/api'
export default {
  data(){
    return{
      form: {
        id: this.$store.state.userInfo.id,
        username: this.$store.state.userInfo.username,
        nickname: this.$store.state.userInfo.nickname,
        email: this.$store.state.userInfo.email,
        right: '',
        content: '',
      },
    }
  },
  created(){
  },
  mounted(){
    
  },
  methods:{
    async onSubmit(){
      if(this.form.content == ''){
        this.$message({
          type:'warning',
          message:'申请理由不能为空！'
        })
      }else if(this.form.right == ''){
        this.$message({
          type:'warning',
          message:'申请权限不能为空！'
        })
      }else{
        console.log(this.form.content);
        console.log(this.form.right);
        const { data: res } = await SubmitApplication(this.form)
        if(res.status != 0) return this.$message.error(res.message)
        this.$message.success(res.message)
      }
    }
  }
}
</script>

<style scoped>
.box{
    width: 90%;
    /* height: 650px; */
    margin: 0 auto;
}
.title{
    padding: 20px 0;
    padding-top: 40px;
}
.title h3{
    font-size: 20px;
    color: #543b24;
    padding: 2px 0;
}
hr{
    height: 5px;
    background: #543b24;
    border: none;
    border-radius: 2px;
}
.el-form{
    margin-top: 20px;
}
.el-form-item {
    margin-bottom: 40px;
}
::v-deep .ql-editor{
    min-height: 180px;
    background-color: #f7f7f7;
}
</style>