<template>
    <div>
        <div class="box">
            <div class="title">
                <h3>安全中心</h3>
                <hr>
            </div>
            <div class="root">
                <el-form ref="passwordForm" :rules="formRules" :model="form" label-width="150px">
                    <el-form-item label="旧密码：" prop="oldPwd" name="oldPwd">
                        <el-input v-model="form.oldPwd"></el-input>
                    </el-form-item>
                    <el-form-item label="新密码：" prop="newPwd" name="newPwd">
                        <el-input v-model="form.newPwd"></el-input>
                    </el-form-item>
                    <el-form-item label="确认新密码：" prop="newPwd2" name="newPwd2">
                        <el-input v-model="form.newPwd2"></el-input>
                    </el-form-item>
                </el-form>
                <el-button type="primary" @click="updatePwd">保存修改</el-button>
                <el-button @click="resetFn">重置</el-button>
            </div>
        </div>
    </div>
</template>

<script>
import { updatePasswordAPI } from '@/api'
export default {
    data() {
      const samePwd = (rule,value,callback) => {
        if(this.form.oldPwd === value){
            callback(new Error('新旧密码不能相同！'))
        }else{
            callback()
        }
      };
      // 验证两次新密码是否一致
      var validatePass = (rule, value, callback) => {
        if (value !== this.form.newPwd) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };
      return {
        form: {
          oldPwd: '',
          newPwd: '',
          newPwd2: '',
        },
        formRules:{
            oldPwd:[
                {required:true, message:'密码不能为空', trigger:'blur'},
                {
                    pattern: /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,20}$/,
                    message: "密码长度为6-20位，必须且只能包括数字、字母",
                    trigger: "blur"
                }
            ],
            newPwd:[
                {required:true, message:'密码不能为空', trigger:'blur'},
                {
                    pattern: /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,20}$/,
                    message: "密码长度为6-20位，必须且只能包括数字、字母",
                    trigger: "blur"
                },
                {validator:samePwd, trigger: 'blur'}
            ],
            newPwd2:[
                {required:true, message:'密码不能为空', trigger:'blur'},
                {
                    pattern: /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,20}$/,
                    message: "密码长度为6-20位，必须且只能包括数字、字母",
                    trigger: "blur"
                },
                {validator:validatePass, trigger: 'blur'}
            ]
        }
      }
    },
    methods: {
        updatePwd(){
            this.$refs.passwordForm.validate( async valid =>{
                if(valid){
                    const { data: res } = await updatePasswordAPI(this.form)
                    if(res.status !== 0) return this.$mesaage.error('更新密码失败！')
                    this.$message.success('更新密码成功！请重新登陆！')
                    this.$refs.passwordForm.resetFields()
                    this.$store.commit('updateToken','')
                    this.$store.commit('updateUserInfo',{})
                    this.$router.push('/')
                }else{
                    return false
                }
            })
        },
        resetFn(){
            this.$refs.passwordForm.resetFields()
        }
    }
}
</script>

<style scoped>
.box{
    width: 90%;
    height: 650px;
    margin: 0 auto;
}
.root{
    background: #fff;
    padding: 30px;
    border-radius: 5px;
}
.title{
    padding: 20px 0;
    padding-top: 40px;
}
.title h3{
    font-size: 20px;
    color: #543b24;
    padding: 2px 0;
}
hr{
    height: 5px;
    background: #543b24;
    border: none;
    border-radius: 2px;
}
.el-form-item__content{
    margin-left: 90px;
    width: 300px;
}
</style>